body{
  background-color: #9266ff;
}
.App{
  background-color: #594685;
  min-height:500px;
  width: 400px;
  margin: 40px auto;
  font-family: cursive; 
}

#to-do-form input{
  background-color: rgb(106, 101, 158);
  border: 0; 
  color: rgb(230, 189, 189);
  width: 200px;
  height: 50px;
  margin: 20px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 10px;
  font-family: monospace    ; 
}
#to-do-form input::placeholder{
  color: rgba(255,255,255,0.5);
}
#to-do-form input:focus{
  outline: none;
}
#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color:rgb(235, 200, 1);
  cursor: pointer;
  color : rgb(41, 163, 163);
  font-family: cursive; 
}
#to-do-form button:focus{
  outline: none;
}